<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item>
          <el-button type="primary" @click="getTbList">刷新</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="addForm">

        <el-form-item label="企业名称" prop="entId" v-show="entData.length>1">
          <!-- <el-input auto-complete="off" v-model="addForm.entId"></el-input> -->
          <el-select v-model="addForm.entId" filterable size="medium" placeholder="请选择">
            <el-option v-for="(item, index) in entData" :key="index" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="能源名称" prop="energyId">
          <el-select v-model="addForm.energyId" filterable placeholder="请选择" @change="changeEnergyName">
            <el-option v-for="item in energyIdOptions" :key="item.dataValue" :label="item.dataText" :value="item.dataValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="能源种类" prop="energyType">
          <el-select v-model="addForm.energyType" filterable placeholder="请选择" @change="changeEnergyName">
            <el-option v-for="item in energyTypeOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="自定义能源名称" prop="energyName">
          <el-input auto-complete="off" v-model="addForm.energyName"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
export default {
  name: "entEnergy",
  data() {
    return {
      entData: [],
      energyIdOptions: [],
      energyTypeOptions: [
        { dataText: "购入能源", dataValue: "1" },
        {
          dataText: "产出能源",
          dataValue: "2"
        },
        { dataText: "工艺产出能源", dataValue: "3" },
        { dataText: "自产自用能源", dataValue: "4" }
      ],
      // 查询表单
      searchForm: {
        formdata: {},
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头

        {
          label: "企业名称",
          prop: "entId",
          align: "left",
          hidden: false,
          sortable: true,
          displace: {}
        },
        {
          label: "能源类型标准名称",
          prop: "energyId",
          align: "left",
          sortable: true,
          displace: {}
        },
        {
          label: "自定义能源名称",
          prop: "energyName",
          align: "left",
          sortable: true
        },
        {
          label: "能源种类",
          prop: "energyType",
          align: "left",
          sortable: true,
          displace: {
            1: "购入能源",
            2: "产出能源",
            3: "工艺产出能源",
            4: "自产自用能源"
          }
        }
      ],
      dialogVisible: false,
      addForm: { entId: "", energyName: "", energyId: "", energyType: "" },
      rules: this.eachValidate({
        entId: { required: { required: true, trigger: "change" } },
        energyName: { maxString: "20" },
        energyId: { required: { required: true, trigger: "change" } },
        energyType: { required: { required: true, trigger: "change" } }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id
        ? "编辑企业需采集的能源资料表"
        : "新增企业需采集的能源资料表";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getEnt() {
      this.$ajax.get("rest/enterprise/list").then(res => {
        if (res.data.status) {
          this.entData = res.data.records;
          if (res.data.records.length > 1) {
            let toObj = {};
            this.$each(res.data.records, item => {
              toObj[item.id] = item.name;
            });
            this.colsData[0].displace = toObj;
          } else {
            this.colsData[0].hidden = true;
          }
        }
      });
    },
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/entEnergy/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    getEnergyIdInfo() {
      this.$ajax.get("rest/entEnergy/energyId/get").then(res => {
        if (res.data.status) {
          this.energyIdOptions = res.data.records;

          let toObj = {};
          this.$each(res.data.records, item => {
            toObj[item.dataValue] = item.dataText;
          });
          this.colsData[1].displace = toObj;
        }
      });
    },
    changeEnergyName() {
      let typeName = ["购入", "产出", "工艺产出", "自产自用"];
      let energys = this.energyIdOptions;
      if (this.addForm.energyId && this.addForm.energyType) {
        let energyName = "";
        for (let i = 0; i < energys.length; i++) {
          if (energys[i].dataValue == this.addForm.energyId) {
            energyName = energys[i].dataText;
          }
        }
        this.addForm.energyName =
          energyName + "(" + typeName[this.addForm.energyType] + ")";
      }
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          entId: "",
          energyName: "",
          energyId: "",
          energyType: ""
        };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/entEnergy", this.addForm).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.dialogVisible = false;
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/entEnergy", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getEnergyIdInfo();
    this.getEnt();
    this.getTbList();
  }
};
</script>



